import React, {Component} from 'react';
import store from '../store';
import {connect} from 'react-redux';
import Board from '../components/Board';
import io from 'socket.io-client';
import {getBoardNotes, getBoard} from '../actions/board';
import {getAllNotes} from '../actions/note';

// const socket = io('', { path: '/api/board/1' });

class BoardContainer extends Component {

  componentWillMount() {
    this.props.onBoardEnter();
    const { dispatch, board} = this.props;

    const boardId = board.id;

    // dispatch(getBoardNotes(boardId));

  }

  render() {
    console.log(this.props);
    return (
      <Board {...this.props} />
    );
  }
}


const mapStateToProps = (state) => ({board: state.board.selectedBoard});

const mapDispatchToProps = (dispatch, ownProps) => (
  {
    onBoardEnter: function() {
      console.log(ownProps);
      const boardId = ownProps.board.id;
      dispatch(getBoard(boardId));
      dispatch(getAllNotes({boardId}));
    }
  });

export default connect(mapStateToProps, mapDispatchToProps)(BoardContainer);



